<template>
    <div id="singer">
        <div class="music-container">
            <!-- 选项 -->
            <div class="option">
                <div class="type-list">
                   <div :class="['item', item.id==type? 'active': '']" v-for="(item, index) in typeList" :key="index" @click="type=item.id">{{item.type}}</div>
                </div>
                <div class="arrea-list">
                    <div :class="['item', item.id==area? 'active' : '']" v-for="(item, index) in areaList" :key="index" @click="area=item.id">{{item.area}}</div>
                </div>
                <div class="initial-list">
                    <div :class="['item', item==initial? 'active' : '']" v-for="(item, index) in initialList" :key="index" @click="initial=item">{{item}}</div>
                </div>
            </div>
            <!-- 主体 -->
            <div class="singer-list">
                <!-- 歌手列表 -->
                <div class="singer-container">
                    <div class="item" v-for="(item, index) in artists" :key="index">
                        <img v-lazy="item.picUrl" alt="">
                        <div class="mask">
                            <div class="name" @click="goSingerDetail(item.id, item.name)">{{item.name}}</div>
                        </div>
                    </div>
                </div>
                <!-- 分页器 -->
                <div class="pagination" v-if="paginationShow">
                    <el-pagination :background="true" layout="total, prev, pager, next ,jumper" :page-size="limit" :total="total" @current-change="handleCurrentChange"></el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { req_singer } from "../network/singer/index.js";
export default {
  name: "Singer",
  data() {
    return {
      // 当前选择
      type: -1,
      area: -1,
      initial: "a",

      // 歌手类型
      typeList: [
        { id: -1, type: "全部" },
        { id: 1, type: "男歌手" },
        { id: 2, type: "女歌手" },
        { id: 3, type: "乐队" },
      ],
      // 歌手地区
      areaList: [
        { id: -1, area: "全部" },
        { id: 7, area: "华语" },
        { id: 96, area: "欧美" },
        { id: 8, area: "日本" },
        { id: 16, area: "韩国" },
        { id: 0, area: "其他" },
      ],
      // 歌手名（首字母）
      initialList: [
        "a",
        "b",
        "c",
        "d",
        "e",
        "f",
        "g",
        "h",
        "i",
        "j",
        "k",
        "l",
        "m",
        "n",
        "o",
        "p",
        "q",
        "r",
        "s",
        "t",
        "u",
        "v",
        "w",
        "x",
        "y",
        "z",
      ],
      
      // 歌手列表 
      artists: [],
      limit: 30,
      total: 900,
      page: 0,
      
      // 分页器
      paginationShow: false,
    };
  },
  watch: {
    type: function() {
      this.getSingerList();
    },
    area: function() {
      this.getSingerList();
    },
    initial: function() {
      this.getSingerList();
    },
  },
  created() {
    this.getSingerList();
  },
  methods: {
    // 请求歌手列表
    getSingerList() {
      // 计算跳过多少数量
      let offset;
      if(this.page == 0) {
          // 排除第一次请求（页面初始化）
          offset = 0;
      }else {
          offset = this.limit * (this.page - 1);
      }
    
      req_singer(this, {
          area: this.area,
          type: this.type,
          initial: this.initial,
          offset,
          limit: this.limit,
      }).then(res => {
          this.artists = res.artists;
          this.page++;
          this.paginationShow = true;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 处理分页器
    handleCurrentChange(newPage) {
         this.page = newPage;
         this.getSingerList();
    },
    goSingerDetail(id, name) {
      this.$router.push({
      path: `/singerDetail/${new Date().getTime()}`,
      query: {
        id,
        name,
      }
    })
   },
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
#singer {
  .music-container {
    width: 1000px;
    .option {
      margin-top: 20px;
      .type-list {
        @include flex(flex-start);
        .item {
          padding: 10px 12px;
          text-align: center;
          @include font(16px, 20px, $colorC);
          cursor: pointer;
          margin-right: 12px;
          border-radius: 20px;
        }
        .active {
          background-color: $colorA;
          color: #fff;
        }
      }
      .arrea-list {
        @include flex(flex-start);
        margin: 12px 0px;
        .item {
          cursor: pointer;
          padding: 10px 12px;
          text-align: center;
          margin-right: 12px;
          border-radius: 20px;
          @include font(16px, 20px, $colorC);
        }
        .active {
          background-color: $colorA;
          color: #fff;
        }
      }
      .initial-list {
        @include flex(flex-start);
        .item {
          cursor: pointer;
          padding: 10px 12px;
        }
        .active {
          background-color: $colorA;
          color: #fff;
        }
      }
    }
    .singer-list {
      margin-top: 30px;
      .singer-container {
        @include flex();
        flex-wrap: wrap;
        .item {
          position: relative;
          width: 160px;
          height: 160px;
          margin-bottom: 12px;
          img {
            display: block;
            width: 100%;
            height: 160px;
          }
          .mask {
            @include position();
            background-color: rgba($color: #000000, $alpha: 0.4);
            z-index: 2;
            @include flex(center, center);
            opacity: 0;
            .name {
              width: 100px;
              height: 20px;
              line-height: 20px;
              @include fontHidden();
              text-align: center;
              border-radius: 12px;
              border: 2px solid $colorA;
              color: #fff;
              cursor: pointer;
              &:hover {
                color: $colorA;
                background-color: #fff;
              }
            }
          }
          &:hover {
            .mask {
              opacity: 1;
            }
          }
        }
      }

      .pagination {
        @include flex(center);
        margin-top: 20px;
        // 修改分页器的颜色
        .el-pagination.is-background .el-pager li:not(.disabled).active {
          background-color: #31c27c;
          color: #fff;
        }
      }
    }
  }
}
</style>